<template>
  <div class="gz-box">
    <van-tabs v-model="active">
      <van-tab title="">
        <div class="item-box" v-for="item in values" :key="item.id" @click="detail" :vid="item.id">
          <div class="img">
            <img :src="item.headportrait" alt width="90px" height="90px" />
          </div>
          <div class="name">:{{item.username}}</div>
          <div class="sex">:{{item.sex}}</div>
          <div class="monologue">
            :
            <span>{{item.monologue}}</span>
          </div>
          <div class="income">
            :
            <span>{{item.income}}</span>
          </div>
          <div class="house">
            :
            <span>{{item.house}}</span>
          </div>
        </div>
      </van-tab>
      <van-tab title="">
        <div class="item-box" v-for="item in values2" :key="item.id" @click="detail" :vid="item.id">
          <div class="img">
            <img :src="item.headportrait" alt width="90px" height="90px" />
          </div>
          <div class="name">:{{item.username}}</div>
          <div class="sex">:{{item.sex}}</div>
          <div class="monologue">
            :
            <span>{{item.monologue}}</span>
          </div>
          <div class="income">
            :
            <span>{{item.income}}</span>
          </div>
          <div class="house">
            :
            <span>{{item.house}}</span>
          </div>
          <div class="addgz" v-on:click="addgzs" :id="item.id"></div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
import { Toast } from "vant";
export default {
  data() {
    return {
      active: 2,
      values: "",
      values2: ""
    };
  },
  methods: {
    addgzs(data) {
      let odata = data;
      // console.log(data.srcElement.id);
      axios({
        method: "post",
        url: "http://39.97.226.25:8082/user/saveOrUpdateAttention",
        data: qs.stringify({
          attention: localStorage.getItem("userId"),
          focused: data.srcElement.id
        })
      }).then(data => {
        // console.log(data);

        switch (data.data) {
          case 0:
            Toast.fail("!");
            break;
          case 1:
            Toast.success("!");
            odata.target.innerText = "";
            break;
          case 2:
            Toast.success("!");
            console.log(odata);
            odata.target.innerText = "";
        }
      });
    },
    detail(data) {
      this.$router.push({
        path: "/detail",
        query: { userId: data.srcElement.offsetParent.attributes[1].nodeValue }
      });
    }
  },
  beforeCreate() {
    this.$store.commit("lefttitle", ""); //
    this.$store.commit("righttitle", ""); //
    this.$store.commit("changeTitle", ""); //
    this.$store.commit("footercheck", "myself"); //

    axios({
      method: "post",
      url: "http://39.97.226.25:8082/user/myFocused",
      data: qs.stringify({ id: localStorage.getItem("userId") })
    }).then(data => {
      // console.log(data); //
      this.values = data.data;
    });

    axios({
      method: "post",
      url: "http://39.97.226.25:8082/user/myAttention",
      data: qs.stringify({ id: localStorage.getItem("userId") })
    }).then(data => {
      // console.log(data); //
      this.values2 = data.data;
    });
  }
};
</script>
<style scoped>
.gz-box {
  margin-bottom: 50px;
}
.item-box {
  height: 100px;
  background: #fafafa;
  position: relative;
  box-sizing: border-box;
  padding: 5px 15px;
  margin-bottom: 10px;
}
.img {
  width: 90px;
  height: 90px;
  display: inline-block;
  float: left;
}
.name {
  font-size: 14px;
  text-align: left;
  padding-left: 110px;
  box-sizing: border-box;
  color: #323233;
}
.sex {
  font-size: 12px;
  text-align: left;
  padding-left: 110px;
  box-sizing: border-box;
}
.monologue {
  color: #7d7e80;
  font-size: 12px;
  padding-left: 110px;
  box-sizing: border-box;
  text-align: left;
  padding-right: 60px;
}
.monologue span {
  display: block;
  height: 38px;
  overflow: hidden;
  word-wrap: break-word;
}
.income {
  position: absolute;
  right: 15px;
  top: 5px;
}
.income span {
  color: red;
  font-weight: 700;
}
.house {
  position: absolute;
  right: 15px;
  top: 24px;
}
.house span {
  color: red;
  font-weight: 700;
}
.addgz {
  position: absolute;
  bottom: 16px;
  right: 15px;
  width: 65px;
  height: 25px;
  border-radius: 5px;
  line-height: 25px;
  background: pink;
  color: #fff;
}
</style>
