<template>
  <div>
    <ul>
      <li
        v-for="(item,i) in list"
        style="text-align:center;border:1px solid gray;height:100px"
        @click="sat(i)"
      >
        <van-image style="float:left" width="100" height="100" :src="item.headportrait" />
        <p style="margin:20px 0 10px 0">:{{item.hostunit}}</p>
        <p style="margin:10px 0 10px 0">:{{item.hotlistname}}</p>
        <p>:{{item.activitytime}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from "axios";
import "lib-flexible";
export default {
  name: "hd",
  data() {
    return {
      list: []
    };
  },
  mounted() {
    var _this = this;
    // console.log(_this.vname)
    axios({
      method: "post",
      url: "http://39.97.226.25:8082//user/showAllHotlist"
    }).then(data => {
      this.list = data.data;
      // console.log(data)
    });
  },
  methods: {
    sat(i) {
      this.$router.push({ path: "/xq" });
    }
  },
  beforeCreate() {
    this.$store.commit("lefttitle", ""); //
    this.$store.commit("righttitle", ""); //
    this.$store.commit("changeTitle", ""); //
    this.$store.commit("footercheck", "hd"); //
  }
};
</script>
