<template>
  <div>
    <heads></heads>

    <div id="mid" style="overflow:hidden;">
      <van-row>
        <van-uploader
          :after-read="afterRead"
          v-model="fileList"
          multiple
          capture
          @delete="deleteimg"
          :max-count="1"
          upload-text=""
          id="upload"
        />
      </van-row>
      <van-field
        v-model="username"
        clearable
        label=""
        right-icon="question-o"
        placeholder=""
        :error-message="usernameerr"
        @click-right-icon="$toast('')"
      />
      <van-field v-model="sex" label="" placeholder="" readonly @click="show" />
      <van-popup v-model="shows" style="width:100%" position="bottom">
        <van-picker show-toolbar :columns="sexcolumns" @cancel="onCancel" @confirm="onConfirm" />
      </van-popup>

      <van-field
        v-model="age"
        type="number"
        clearable
        label=""
        :error-message="ageerr"
        right-icon="question-o"
        placeholder=""
        @click-right-icon="$toast('18-100')"
      />
      <van-field
        v-model="heights"
        type="number"
        clearable
        label=""
        :error-message="heightserr"
        placeholder=""
        right-icon="question-o"
        @click-right-icon="$toast('cm')"
      />
      <van-field
        v-model="weights"
        type="number"
        clearable
        :error-message="weightserr"
        label=""
        placeholder=""
        right-icon="question-o"
        @click-right-icon="$toast('kg')"
      />
      <van-field
        v-model="constellation"
        label=""
        readonly
        @click="constellationShow"
        placeholder=""
      />
      <van-popup v-model="xzshow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="constellationColumns"
          @cancel="constellationCancel"
          @confirm="constellationConfirm"
        />
      </van-popup>

      <van-field v-model="blood" label="" readonly @click="bloodShows" placeholder="" />
      <van-popup v-model="bloodShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="bloodColumns"
          @cancel="bloodCancel"
          @confirm="bloodConfirm"
        />
      </van-popup>

      <van-field v-model="nation" label="" readonly @click="nationShows" placeholder="" />
      <van-popup v-model="nationShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="nationColumns"
          @cancel="nationCancel"
          @confirm="nationConfirm"
        />
      </van-popup>

      <van-field v-model="eduinfo" label="" readonly @click="eduinfoShows" placeholder="" />
      <van-popup v-model="eduinfoShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="eduinfoColumns"
          @cancel="eduinfoCancel"
          @confirm="eduinfoConfirm"
        />
      </van-popup>

      <van-field v-model="job" label="" readonly @click="jobShows" placeholder="" />
      <van-popup v-model="jobShow" style="width:100%" position="bottom">
        <van-picker show-toolbar :columns="jobColumns" @cancel="jobCancel" @confirm="jobConfirm" />
      </van-popup>

      <van-field v-model="income" label="" readonly @click="incomeShows" placeholder="" />
      <van-popup v-model="incomeShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="incomeColumns"
          @cancel="incomeCancel"
          @confirm="incomeConfirm"
        />
      </van-popup>

      <van-field
        v-model="address"
        label=""
        readonly
        @click="addressShows"
        placeholder=""
      />
      <van-popup v-model="addressShow" style="width:100%" position="bottom">
        <van-area :area-list="areaList" @cancel="addressCancel" @confirm="addressConfirm" />
      </van-popup>

      <van-field v-model="marry" label="" readonly @click="marryShows" placeholder="" />
      <van-popup v-model="marryShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="marryColumns"
          @cancel="marryCancel"
          @confirm="marryConfirm"
        />
      </van-popup>

      <van-field v-model="house" label="" readonly @click="houseShows" placeholder="" />
      <van-popup v-model="houseShow" style="width:100%" position="bottom">
        <van-picker
          show-toolbar
          :columns="houseColumns"
          @cancel="houseCancel"
          @confirm="houseConfirm"
        />
      </van-popup>

      <van-field
        v-model="characters"
        label=""
        type="textarea"
        placeholder=""
        rows="1"
        autosize
        right-icon="question-o"
        @click-right-icon="$toast('')"
      />

      <van-field
        v-model="monologue"
        label=""
        type="textarea"
        placeholder=""
        rows="1"
        autosize
        right-icon="question-o"
        @click-right-icon="$toast('')"
      />

      <van-field
        v-model="hobbies"
        label=""
        type="textarea"
        placeholder=""
        rows="1"
        autosize
        right-icon="question-o"
        @click-right-icon="$toast('')"
      />

      <van-row type="flex" justify="center">
        <van-button type="danger" size="small" id="but" @click="sendimgs"></van-button>
      </van-row>
    </div>
  </div>
</template>
<script>
import heads from "../../components/Tou";
import list from "../../../public/js/list";
import axios from "axios";
export default {
  data() {
    return {
      fileList: [],
      areaList: list,
      shows: false,
      xzshow: false,
      username: "",
      sex: "",
      heights: "",
      sexcolumns: ["", ""],
      constellation: "",
      constellationColumns: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      blood: "",
      bloodShow: false,
      bloodColumns: ["A", "B", "O", "AB"],
      age: "",
      weights: "",
      nation: "",
      nationShow: false,
      nationColumns: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      eduinfo: "",
      eduinfoShow: false,
      eduinfoColumns: ["", "", "", "", ""],
      job: "",
      jobShow: false,
      jobColumns: ["", "", "", "", ""],
      income: "",
      incomeShow: false,
      incomeColumns: [
        "1-5",
        "5-10",
        "10-15",
        "15-20",
        "20-35",
        "35-50",
        "50-100",
        "100"
      ],
      address: "",
      addressShow: false,
      marry: "",
      marryShow: false,
      marryColumns: ["", "", "", "", ""],
      house: "",
      houseShow: false,
      houseColumns: ["", ""],
      characters: "",
      monologue: "",
      hobbies: "",
      imgdata: [],
      usernameerr: "",
      ageerr: "",
      heightserr: "",
      weightserr: "",
      bool: false,
      agebool: false,
      heightsboll: false,
      weightsboll: false
    };
  },
  components: {
    heads
  },
  beforeCreate() {
    this.$store.commit("lefttitle", ""); //
    this.$store.commit("righttitle", ""); //
    this.$store.commit("changeTitle", ""); //
  },
  mounted() {},
  watch: {
    username: function(newinfo) {
      let reg = /^[\u4E00-\u9FA5\uf900-\ufa2ds]{2,20}$/;
      this.bool = reg.test(newinfo);
      console.log(this.bool);
      if (this.bool) {
        this.usernameerr = "";
      } else {
        this.usernameerr = "";
      }
    },
    age: function(newinfo) {
      console.log(newinfo);
      if (newinfo >= 18 && newinfo < 100) {
        this.ageerr = "";
        this.agebool = true;
      } else {
        this.ageerr = "18-100";
        this.agebool = false;
      }
    },
    heights: function(newinfo) {
      if (newinfo > 100 && newinfo < 300) {
        this.heightserr = "";
        this.heightsboll = true;
      } else {
        this.heightserr = "100-300cm";
        this.heightsboll = false;
      }
    },
    weights: function(newinfo) {
      if (newinfo > 0 && newinfo < 200) {
        this.weightserr = "";
        this.weightsbool = true;
      } else {
        this.weightserr = "0-200kg";
        this.weightsboll = false;
      }
    }
  },
  methods: {
    afterRead(file) {
      // 
      console.log(file);
      //   this.headerImg = file.content;
      //   let fordata = new FormData();
      //   fordata.append("avatarfile", file.file);
      //   this.imgdata = fordata;
      //   console.log(fordata.get("avatarfile"));
      this.imgdata = file.file;
    },
    deleteimg(file) {
      this.imgdata = "";
      console.log(this.imgdata);
    },

    sendimgs() {
      let mark = true;
      let objinfo = {
        username: this.username,
        sex: this.sex,
        age: this.age,
        heights: this.heights,
        weights: this.weights,
        constellation: this.constellation,
        blood: this.blood,
        nation: this.nation,
        eduinfo: this.eduinfo,
        job: this.job,
        income: this.income,
        address: this.address,
        marry: this.marry,
        house: this.house,
        hobbies: this.hobbies,
        monologue: this.monologue,
        characters: this.characters,
        uploadFile: this.imgdata,
        id: 17
      };
      let fordata = new FormData();

      console.log(fordata.get("uploadFile"));
      let tipinfo = "";
      for (let key in objinfo) {
        // console.log(objinfo[key],key);
        if (objinfo[key] == "") {
          console.log(objinfo.key, 1, key);
          switch (key) {
            case "username":
              tipinfo = "";
              break;
            case "sex":
              tipinfo = "";
              break;
            case "age":
              tipinfo = "";
              break;
            case "heights":
              tipinfo = "";
              break;
            case "weights":
              tipinfo = "";
              break;
            case "constellation":
              tipinfo = "";
              break;
            case "blood":
              tipinfo = "";
              break;
            case "nation":
              tipinfo = "";
              break;
            case "eduinfo":
              tipinfo = "";
              break;
            case "job":
              tipinfo = "";
              break;
            case "income":
              tipinfo = "";
              break;
            case "address":
              tipinfo = "";
              break;
            case "marry":
              tipinfo = "";
              break;
            case "hobbies":
              tipinfo = "";
              break;
            case "monologue":
              tipinfo = "";
              break;
            case "characters":
              tipinfo = "";
              break;
          }
          this.$toast(tipinfo);
          return (mark = false);
        }
      }
      for (let key in objinfo) {
        fordata.append(key, objinfo[key]);
      }

      if (
        mark &&
        this.weightsbool &&
        this.bool &&
        this.heightsboll &&
        this.agebool
      ) {
        console.log(fordata);
        this.$store.state.selfInfo = objinfo
        console.log(this.$store.state.selfInfo)
            axios({
              url: "http://39.97.226.25:8082/user/updateUserInfo",
              method:"post",
              data:fordata,
              headers:{
                  'Content-Type':'multipart/form-data'
              }
           }).then((data)=>{
               if(data.data==1){
                 this.$router.push("/addInfo")
               }
           }).catch(function(err){
               console.log(err)
           })

      } else {
        this.$toast("");
      }
    },
    show() {
      this.shows = true;
    },
    onConfirm(value, index) {
      this.shows = false;
      this.sex = value;
    },
    onCancel() {
      this.shows = false;
    },
    constellationShow() {
      this.xzshow = true;
    },
    constellationConfirm(value, index) {
      this.xzshow = false;
      this.constellation = value;
    },
    constellationCancel() {
      this.xzshow = false;
    },
    bloodShows() {
      this.bloodShow = true;
    },
    bloodConfirm(value, index) {
      this.bloodShow = false;
      this.blood = value;
    },
    bloodCancel() {
      this.bloodShow = false;
    },
    nationShows() {
      this.nationShow = true;
    },
    nationConfirm(value, index) {
      this.nationShow = false;
      this.nation = value;
    },
    nationCancel() {
      this.eduinfoShow = false;
    },
    eduinfoShows() {
      this.eduinfoShow = true;
    },
    eduinfoConfirm(value, index) {
      this.eduinfoShow = false;
      this.eduinfo = value;
    },
    eduinfoCancel() {
      this.eduinfoShow = false;
    },
    jobShows() {
      this.jobShow = true;
    },
    jobConfirm(value, index) {
      this.jobShow = false;
      this.job = value;
    },
    jobCancel() {
      this.jobShow = false;
    },
    incomeShows() {
      this.incomeShow = true;
    },
    incomeConfirm(value, index) {
      this.incomeShow = false;
      this.income = value;
    },
    incomeCancel() {
      this.incomeShow = false;
    },
    addressShows() {
      this.addressShow = true;
    },
    addressConfirm(value) {
      this.addressShow = false;
      let addressinfo = "";
      for (let i = 0; i < value.length; i++) {
        if (addressinfo != value[i].name) {
          addressinfo = addressinfo + value[i].name;
        }
      }
      this.address = addressinfo;
    },
    addressCancel() {
      this.addressShow = false;
    },
    marryShows() {
      this.marryShow = true;
    },
    marryConfirm(value, index) {
      this.marryShow = false;
      this.marry = value;
    },
    marryCancel() {
      this.marryShow = false;
    },
    marryShows() {
      this.marryShow = true;
    },
    marryConfirm(value, index) {
      this.marryShow = false;
      this.marry = value;
    },
    marryCancel() {
      this.marryShow = false;
    },
    houseShows() {
      this.houseShow = true;
    },
    houseConfirm(value, index) {
      this.houseShow = false;
      this.house = value;
    },
    houseCancel() {
      this.houseShow = false;
    }
  }
};
</script>
<style scoped>
#upload {
  text-align: left !important;
}
#mid {
  text-align: left !important
}
#but {
  margin: 30px 0;
  background: pink;
  border-color: pink;
}
</style>
