import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userID: "", //id
    email: "", //email
    password: "", //
    title: "", //
    leftTitle: "", //
    rightTitle: "", //
    fcheck: "", //
    values: "", //
    //zpystatestart
    selfInfo: null, // 
    favoredType: null ,//
    receiveinfo:null
    //end
  },
  mutations: {
    addmsg(
      state,
      value //
    ) {
      state.values = value;
    },
    newinfos(
      state,
      value //
    ) {
      state.selfInfo = value;
    },
    adduserID(
      state,
      value //id
    ) {
      localStorage.setItem("userId", value);
      state.userID = value;
    },
    addEmail(
      state,
      value //id
    ) {
      state.email = value;
    },
    changeTitle(
      state,
      value //
    ) {
      state.title = value;
    },
    footercheck(
      state,
      value //
    ) {
      state.fcheck = value;
    },
    lefttitle(
      state,
      value //
    ) {
      state.leftTitle = value;
    },
    righttitle(
      state,
      value //
    ) {
      state.rightTitle = value;
    }
  },
  actions: {}
});
